@import "../../../../stylesheets/mixins.scss";
@import "../../../../stylesheets/variables.scss";

.block_footer {
  padding-top: 60px;
  text-align: center;
  color: $mineShaft;

  @mixin mobile {
    font-size: 24px;
  }

  &__form {
    margin-top: 30px;

    &-wrapper {
      padding-bottom: 58px;
      border-bottom: 1px solid rgba($darkGray, .2);
    }

    &__field {
      border-radius: 3px;
      border: 1px solid $tiffanyBlue;
      padding: 0 19px;
      font-size: 14px;
      margin-right: 14px;
      size: 300px 44px;

      @mixin mobile {
        height: 60px;
        font-size: 24px;
      }

      &::placeholder {
        color: $tiffanyBlue;
      }
    }

    &__submit {
      background: $moodyBlue;
      color: $white;
      border-radius: 5px;
      cursor: pointer;
      font-size: 16px;
      size: 200px 46px;

      @mixin mobile {
        height: 60px;
      }
    }
  }

  &__copy {
    padding-top: 43px;
    padding-bottom: 58px;

    @mixin mobile {
      padding-bottom: 32px;
    }

    &__text {
      color: $tiffanyBlue;
      font-size: 14px;
      margin-bottom: 25px;

      @mixin mobile {
        font-size: 18px;
      }
    }
  }

  &__logo {
    margin-bottom: 20px;
  }

  &__social {
    text-align: center;

    &_icon {
      background-repeat: no-repeat;
      background-position: center;
      display: inline-block;
      vertical-align: middle;
      size: 21px;

      @mixin mobile {
        size: 42px;
      }

      &:not(:last-child) {
        margin-right: 10px;
      }

      &_vk {
        background-image: url('data:image/png;base64,');

        @mixin mobile {
          background-image: url('data:image/png;base64,');
        }
      }

      &_fb {
        background-image: url('data:image/png;base64,');

        @mixin mobile {
          background-image: url('data:image/png;base64,');
        }
      }

      &_inst {
        background-image: url('data:image/png;base64,');

        @mixin mobile {
          background-image: url('data:image/png;base64,');
        }
      }
    }
  }
}



